/* Animaciones */
@keyframes fadeIn {
  from {
      opacity: 0;
  }
  to {
      opacity: 1;
  }
}

@keyframes slideDown {
  from {
      transform: translateY(-50px);
      opacity: 0;
  }
  to {
      transform: translateY(0);
      opacity: 1;
  }
}

/* 🟢 Botón flotante de búsqueda */
.search-button {
  position: fixed;
  bottom: 20px;
  right: 20px;
  background-color: #ffc400;
  color: white;
  border: none;
  padding: 12px 20px;
  border-radius: 50px;
  font-size: 16px;
  cursor: pointer;
  transition: 0.3s ease;
  z-index: 4;
}

.search-button:hover {
  background-color: #0056b3;
}

